.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.loginBtn{
  border: 4px solid coral;
  background-color: white;
  border-radius: 5px;
  color: coral;
  font-size:20px;
  padding:10px;
  margin: 10px;
  width: 200px;
}
.message{
  font-family: arial;
  font-size: 20px;
  margin:10px;
}
.inputDiv{
  font-family: arial;
  font-size: 20px;
  margin:10px;
}
.inputDiv>input[type=text], .nextletterDiv>input[type=text] {
  border-radius:5px;
  border-color:coral;
  font-family: arial;
  font-size: 20px;
  height: 30px;
  width:200px;
}
.inputDiv>input[type=checkbox]{
  height:20px;
  width: 20px;
}
.nextletterDiv{
  font-family: arial;
  font-size: 20px;
  margin:10px;
}
.gameDiv{
  font-family: arial;
  font-size: 20px;
  padding:10px;
  margin: 5px;
}
.gameDiv > input[type="text"]{
  border-radius:5px;
  border-color:coral;
  font-family: arial;
  font-size: 20px;
  height: 30px;
  width:200px;
}
.gameDiv > button{
  border: 4px solid coral;
  background-color: white;
  border-radius: 5px;
  color: coral;
  font-size:20px;
  font-weigth: bold;
  padding:10px;
  margin-left: 20px;
}
.gameDiv>.playAgainBtn{
  border: 4px solid black;
  background-color: white;
  border-radius: 5px;
  color: black;
  font-size:15px;
  font-weigth: bold;
  padding:5px;
  margin: 10px;
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



